.title {
  font-size: 1rem;
}
.container {
  padding: 0.2rem;
}
.container.grid {
  width: 50%;
}
.container.list {
  width: 100%;
}
.video {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  transition: transform 250ms ease-in;
  border: 1px solid lightgray;
  cursor: pointer;
}
.video:hover {
  transform: scale(1.02);
}
.thumbnail {
  width: 40%;
  height: 100%;
}
.metadata {
  margin-left: 0.2rem;
}
.title,
.channel {
  margin: 0;
  font-size: 0.8rem;
}

.channel {
  font-size: 0.6rem;
}

@media screen and (max-width: 48rem) {
  .container.grid {
    width: 100%;
  }
}
