.header {
  display: flex;
  height: 4rem;
  padding: 0.5rem 1em;
  background: black;
}

.menu {
  background: none;
  color: white;
  border: none;
  margin: 0 1.5rem;
}
.menu:hover {
  cursor: pointer;
}

.logo {
  display: flex;
  align-items: center;
  margin-right: 1em;
  text-decoration: none;
  color: white;
}
.title {
  padding-left: 10px;
  height: 50px;
}
.input {
  margin-left: 4rem;
  flex-basis: 50%;
  font-size: 1.2rem;
  outline: 0;
}

.btn {
  background: darkgray;
}

.btnImg {
  height: 100%;
  padding: 0.5rem 0.2rem;
  outline: 0;
}
